import React, { useState, useEffect } from 'react';
import { Layout, Spin, Row, Col, Image } from 'antd';
import { useCookies } from 'react-cookie';
import axios from 'axios';

import { serverUrl } from './config';
import './App.css';
import 'antd/dist/antd.css';

import AppHeader from './components/common/header';
import AppFooter from './components/common/footer';

import AppHome from './views/home';
import AppLogin from './components/login/login';

const { Header, Content, Footer } = Layout;

const App = (props) => {
  const [isLoadingPage, setIsLoadingPage] = useState(true);
  const [isAuth, setIsAuth] = useState(false);
  const [cookies, setCookie] = useCookies(['session']);

  useEffect(() => {
    let session = cookies.session;

    if (!session ){
      setIsAuth(false);
      setIsLoadingPage(false);
      return;
    }
    axios({
      url: serverUrl,
      method: "GET",
      params: {
        function: "checklogin",
        session: session
      }
    })
    .then(response => {
      if(!response.data.login) throw new Error("Session wrong or expired");

      setIsLoadingPage(false);
      setIsAuth(true);
    })
    .catch(error => {
      const { response } = error;
      if (response) {
        const { request, ...errorObject } = response; // take everything but 'request'
        console.log(errorObject);
        //return openNotification("warning", "Login Error", errorObject.data.message);
      }
      else {
        //return openNotification("error", "Login Error", error.message);
      }
      setIsLoadingPage(false);
      setIsAuth(false);
    })
  }, []);

  return (
    <Layout className="mainLayout">
      <Header>
        <AppHeader />
      </Header>
      <Content>
      {
          !isLoadingPage ? 
            (
              <AppHome />
            ) :
            (
              <div className="ccs-loading-container">
                <Spin className="ccs-loading-container-spinner" size="large"  />
              </div>
            )
      }
      </Content>
      <Footer>
       <AppFooter />
      </Footer>
    </Layout>
  );
}

export default App;